      <br>*FOR PRODUCT INFORMATION, USE THE SEARCH BAR AT THE TOP OF THE PAGE.</div>
  </div>

  <swiper
    :slidesPerView="3"
    :spaceBetween="30"
    :pagination="{
      clickable: true,
    }"
    :modules="modules"
    class="mySwiper2"
    data-aos="fade-left" data-aos-duration="1500" data-aos-once="true">
    <swiper-slide class="helpbox"><div class="help-title2">WHEN CAN I CONNECT TO CUSTOMER CENTER VIA CHAT?</div><div class="help-subtitle2">EVERYDAY FROM 10:00 - 21:00 (WE DO NOT OPEN ON THE LUNAR NEW YEAR'S DAY.)</div></swiper-slide>
    <swiper-slide class="helpbox"><div class="help-title2">CAN I RETURN ITEMS?</div><div class="help-subtitle2">YOU HAVE 365 DAYS TO RETURN YOUR PURCHASE. KEEP YOUR RECEIPT SAFE TO MAKE YOUR RETURN EASIER. YOU CAN RETURN ANY PRODUCT AS LONG AS IT’S IN NEW CONDITION.</div></swiper-slide>
    <swiper-slide class="helpbox"><div class="help-title2">CAN I EXCHANGE / RETURN PRODUCTS PURCHASED ONLINE AT THE IKEA STORE?</div><div class="help-subtitle2">ONLINE PURCHASED PRODUCTS CAN BE RETURNED AT YOUR NEAREST IKEA STORE.</div></swiper-slide>
    <swiper-slide class="helpbox"><div class="help-title2">THE PRODUCT I RECEIVED WAS DAMAGED, WHAT DO I DO?</div><div class="help-subtitle2">IF THERE ARE ANY ITEMS DELIVERED TO YOU DAMAGED, PLEASE CONTACT OUR CUSTOMER SUPPORT CENTER WITH A PHOTO OF THE DAMAGED BOX AND THE ITEM. WE CAN PROCEED AN EXCHANGE ONCE WE CHECK YOUR ORDER NUMBER AND THE PHOTOS PROVIDED.</div></swiper-slide>
    <swiper-slide class="helpbox"><div class="help-title2">I WOULD LIKE TO KNOW THE EXPECTED ARRIVAL DATE OF STOCK.</div><div class="help-subtitle2">UNFORTUNATELY, WE ASK FOR YOUR UNDERSTANDING THAT IT IS DIFFICULT TO INFORM YOU THAT THE EXPECTED WAREHOUSING DATE OF THE SOLD-OUT PRODUCT IS CHANGED DUE TO VARIOUS FACTORS. YOU CAN CHECK IT ON THE IKEA WEBSITE OR MOBILE APP WHEN RESTOCKED IN THE FUTURE, SO PLEASE REFER TO IT.</div></swiper-slide>
  </swiper>

  <div>
  <div class="view-container">
    <div class="help-title">FIND A STORE</div>
    <div class="help-subtitle">ALL OF OUR STORES ARE IN THE <a class="costcolink" href="https://www.costco.com">COSTCO</a>.</div>
    <div id="map"></div>
  </div>
  </div>
</template>

<script>


 // Import Swiper Vue.js components
 import { Swiper, SwiperSlide } from 'swiper/vue';

// Import Swiper styles
import 'swiper/css';

import 'swiper/css/pagination';

import './style.css';

// import required modules
import { Pagination } from 'swiper/modules';


export default {

  components: {
    Swiper,
    SwiperSlide,
  },
  
  name: "KakaoMap", // 컴포넌트 이름 지정
  data() {
    return {
      // map 객체 설정
      map: null,
    };
  },
  setup() {
    return {
      modules: [Pagination],
    };
  },
  created() {},
  mounted() {
    if (window.kakao && window.kakao.maps) {
      this.loadMap();
    } else {
      this.loadScript();
    }
  },
  unmounted() {},
  methods: {
    loadScript() {
      const script = document.createElement("script");
      script.src =
      `//dapi.kakao.com/v2/maps/sdk.js?appkey=${encodeURIComponent(process.env.VUE_APP_KAKAO_API_KEY)}&autoload=false`;
      script.onload = () => window.kakao.maps.load(this.loadMap); 

      document.head.appendChild(script);
    },
    // 맵 출력하기
    loadMap() {
      const container = document.getElementById("map"); 
      const options = {
        center: new window.kakao.maps.LatLng(35.87664523599632, 128.73062850362362), 
        level: 5
      };

      this.map = new window.kakao.maps.Map(container, options); 
      this.loadMaker();
    },
    // 지정한 위치에 마커 불러오기
    loadMaker() {
  const markerPositions = [
    new window.kakao.maps.LatLng(35.87664523599632, 128.73062850362000), 
    new window.kakao.maps.LatLng(35.17839140166943, 129.11535837849848),
    new window.kakao.maps.LatLng(35.17839140166943, 129.11535837849848),
    new window.kakao.maps.LatLng(35.906246794034026, 128.61822775774183),
    new window.kakao.maps.LatLng(37.52769606880885, 126.892157490763),
    new window.kakao.maps.LatLng(37.49863630810632, 126.85983648153004),
    new window.kakao.maps.LatLng(37.46152466305041, 127.03651769981565),
    new window.kakao.maps.LatLng(37.59685328233616, 127.0902462843474),
    new window.kakao.maps.LatLng(36.32405030031226, 127.40351149986571),
    new window.kakao.maps.LatLng(37.238268351182796, 127.10885162475817),
    new window.kakao.maps.LatLng(35.235249787819775, 128.838739186512),
    new window.kakao.maps.LatLng(35.57707323678628, 129.35906978502368),
    new window.kakao.maps.LatLng(36.467511684943645, 127.26919975271925),
    new window.kakao.maps.LatLng(37.23795743643579, 127.10891034358224),
    new window.kakao.maps.LatLng(37.38946239051835, 126.63358690828565 ),

  ];

  for (const position of markerPositions) {
    const marker = new window.kakao.maps.Marker({
      position: position
    });

    marker.setMap(this.map);
  }
  },
  },
};
</script>

